import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Profile from '@/components/Profile'
import Msite from '@/components/Msite'
import Order from '@/components/Order'
import Search from '@/components/Search'
import Vipcard from '@/components/Vipcard'
import Login from '@/components/Login'
import City from '@/components/City'
import Forget from '@/components/Forget'
import Service from '@/components/Service'
import QuestionDetail from '@/components/QuestionDetail'
import Download from '@/components/Download'
import Food from '@/components/Food'
import Shop from '@/components/Shop'
import ShopDetail from '@/components/Shop/ShopDetail'
import FoodDetail from '@/components/Shop/FoodDetail'
import ConfirmOrder from '@/components/ConfirmOrder'
import ChooseAddress from '@/components/ConfirmOrder/ChooseAddress'
import Payment from '@/components/ConfirmOrder/Payment'
import Remark from '@/components/ConfirmOrder/Remark'
import Invoice from '@/components/ConfirmOrder/Invoice'
import AddAddress from '@/components/ConfirmOrder/ChooseAddress/AddAddress'
import SearchAddress from '@/components/ConfirmOrder/ChooseAddress/AddAddress/SearchAddress'
Vue.use(Router)

export default new Router({
  routes: [{
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/order',
      name: 'Order',
      component: Order
    },
    {
      path: '/msite',
      name: 'Msite',
      component: Msite
    },
    {
      path: '/vipcard',
      name: 'Vipcard',
      component: Vipcard
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/city/:id',
      name: 'City',
      component: City
    },
    {
      path: '/forget',
      name: 'Forget',
      component: Forget
    },
    {
      path: '/service',
      name: 'Service',
      component: Service,
      children: [{
        path: '/service/questionDetail',
        name: 'QuestionDetail',
        component: QuestionDetail,
      }]
    },
    {
      path: '/download',
      name: 'Download',
      component: Download,
    },
    {
      path: '/food',
      name: 'Food',
      component: Food,
    },
    {
      path: '/shop',
      name: 'Shop',
      component: Shop,
      children: [{
          path: '/shop/shopDetail',
          name: 'ShopDetail',
          component: ShopDetail,
        },
        {
          path: '/shop/foodDetail',
          name: 'FoodDetail',
          component: FoodDetail,
        },
      ]
    },
    {
      path: '/confirmOrder',
      name: 'ConfirmOrder',
      component: ConfirmOrder,
      children: [{
        path: '/ConfirmOrder/ChooseAddress',
        name: 'ChooseAddress',
        component: ChooseAddress,
        children: [{
          path: '/ConfirmOrder/ChooseAddress/AddAddress',
          name: 'AddAddress',
          component: AddAddress,
          children: [{
            path: "/ConfirmOrder/ChooseAddress/AddAddress/SearchAddress",
            name: 'SearchAddress',
            component: SearchAddress,
          }]
        }]
      }, {
        path: '/ConfirmOrder/payment',
        name: 'Payment',
        component: Payment,
      },{
        path: '/ConfirmOrder/remark',
        name: 'Remark',
        component: Remark,
      }, {
        path: '/ConfirmOrder/invoice',
        name: 'Invoice',
        component: Invoice,
      }, ]
    }
  ]
})



